import { useState } from 'react';
import { Row, Col } from 'antd';

function Search(props) {
  const [value, setValue] = useState(''),
    [btnDisabled, setBtnDisabled] = useState(true);

  const checkCharAmount = (e) => {
    const valor = e.target.value;
    if (valor && valor.length >= 3) {
      if (btnDisabled) setBtnDisabled(false);
    } else {
      if (!btnDisabled) setBtnDisabled(true);
    }
    setValue(valor);
  };

  const checkKeyPressed = (e) => {
    if (e.key === 'Enter' && !btnDisabled) {
      props.changeValue(value);
      setValue('');
      setBtnDisabled(true);
    }
  };

  return (
    <Row>
      <Col xs={24} md={14} lg={10}>
        <span
          className="ant-input-group-wrapper ant-input-search ant-input-search-default ant-input-search-with-button"
          style={{ margin: '4rem auto 0' }}
        >
          <span className="ant-input-wrapper ant-input-group">
            <span className="ant-input-affix-wrapper">
              <input
                placeholder="input search text"
                className="ant-input"
                type="text"
                value={value}
                onChange={checkCharAmount}
                onKeyDown={checkKeyPressed}
              />
              <span className="ant-input-suffix">
                <span
                  role="img"
                  aria-label="audio"
                  style={{ fontSize: '16px', color: 'rgb(24, 144, 255)' }}
                  className="anticon anticon-audio"
                >
                  <svg
                    viewBox="64 64 896 896"
                    focusable="false"
                    data-icon="audio"
                    width="1em"
                    height="1em"
                    fill="currentColor"
                    aria-hidden="true"
                  >
                    <path d="M842 454c0-4.4-3.6-8-8-8h-60c-4.4 0-8 3.6-8 8 0 140.3-113.7 254-254 254S258 594.3 258 454c0-4.4-3.6-8-8-8h-60c-4.4 0-8 3.6-8 8 0 168.7 126.6 307.9 290 327.6V884H326.7c-13.7 0-24.7 14.3-24.7 32v36c0 4.4 2.8 8 6.2 8h407.6c3.4 0 6.2-3.6 6.2-8v-36c0-17.7-11-32-24.7-32H548V782.1c165.3-18 294-158 294-328.1zM512 624c93.9 0 170-75.2 170-168V232c0-92.8-76.1-168-170-168s-170 75.2-170 168v224c0 92.8 76.1 168 170 168zm-94-392c0-50.6 41.9-92 94-92s94 41.4 94 92v224c0 50.6-41.9 92-94 92s-94-41.4-94-92V232z"></path>
                  </svg>
                </span>
              </span>
            </span>
            <span className="ant-input-group-addon">
              <button
                type="button"
                className="ant-btn ant-btn-primary ant-input-search-button"
                onClick={() => props.changeValue(value)}
                disabled={btnDisabled}
              >
                <span>search</span>
              </button>
            </span>
          </span>
        </span>
      </Col>
    </Row>
  );
}

export default Search;
